@import url(https://fonts.googleapis.com/css?family=Overlock);
html{
  font-family: 'Overlock', cursive;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
.btn{
  opacity: 0.8;
  transition: 0.3s;
}
.btn:hover{
  opacity: 1.2;
}

button,input{
  outline: none;
}

.todo::-webkit-input-placeholder{
  font-size: 110%;
  font-family: 'Overlock';
  opacity: 10;
}

.todo::-ms-input-placeholder{
  font-size: 110%;
  font-family: 'Overlock';
  opacity: 10;
}

.todo::placeholder{
  font-size: 110%;
  font-family: 'Overlock';
  opacity: 10;
}

.redtodo::-webkit-input-placeholder, .redtodo input{
  color: #eb3b5a;
}

.redtodo::-ms-input-placeholder, .redtodo input{
  color: #eb3b5a;
}

.redtodo::placeholder, .redtodo input{
  color: #eb3b5a;
}

.greentodo::-webkit-input-placeholder, .greentodo input{
  color: #16a085;
}

.greentodo::-ms-input-placeholder, .greentodo input{
  color: #16a085;
}

.greentodo::placeholder, .greentodo input{
  color: #16a085;
}
.yellowtodo::-webkit-input-placeholder, .yellowtodo input, .blacktodo::-webkit-input-placeholder, .blacktodo input{
  color: #000;
}
.yellowtodo::-ms-input-placeholder, .yellowtodo input, .blacktodo::-ms-input-placeholder, .blacktodo input{
  color: #000;
}
.yellowtodo::placeholder, .yellowtodo input, .blacktodo::placeholder, .blacktodo input{
  color: #000;
}
.bluetodo::-webkit-input-placeholder, .bluetodo input{
  color: #2d98da; 
}
.bluetodo::-ms-input-placeholder, .bluetodo input{
  color: #2d98da; 
}
.bluetodo::placeholder, .bluetodo input{
  color: #2d98da; 
}





